<template>
  <div class="wallet_manage">
    <appHead back="true" mid="钱包管理" add="true" color="#fffefe"></appHead>
    <div class="wrap">
      <div>
        <div class="coin_type">比特币 BTC</div>
        <div>
          <div class="my_wallets">
            <div class="wallet">
              <span>我的钱包1</span>
              <img class="delete" src="../../assets/image/delete.png" alt="">
            </div>
            <div class="line"></div>
            <div class="wallet_address">
              <span>钱包地址 :</span>
              <span>dasfjricf************qpnfeofn</span>
            </div>
          </div>
          <div class="my_wallets">
            <div class="wallet">
              <span>我的钱包2</span>
              <img class="delete" src="../../assets/image/delete.png" alt="">
            </div>
            <div class="line"></div>
            <div class="wallet_address">
              <span>钱包地址 :</span>
              <span>dasfjricf************qpnfeofn</span>
            </div>
          </div>
        </div>
        <p class="tip">您最多可以保存10个虚拟币钱包</p>
      </div>
    </div>
  </div>
</template>

<script>
import appHead from '../../commons/appHead'
export default {
  name: 'walletManage',
  data () {
    return {
      msg: '钱包管理'
    }
  },
  components: {
    appHead
  },
  mounted: function () {
    this.init()
  },
  methods: {
    init () {
      $(document).ready(() => {
        $('.delete').click(function () {
          $(this).parents('.my_wallets').css('display', 'none')
        })
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/style/common.css";
@mixin flex_a {
  display: flex;
  align-items: center;
}
@mixin flex_j {
  display: flex;
  justify-content: center;
}
@mixin img {
  display: inline-block;
  position: absolute;
  right: 0.5rem;
  width: 0.7rem;
}
@mixin font($size, $color: white) {
  font-size: $size;
  font-family: "微软雅黑";
  color: $color;
}
.wallet_manage {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  .wrap {
    margin-top: 2rem;
    overflow-y: auto;
    height: 100%;
    @include font(0.7rem,#565656);
    background-color: #f8f8f8;
    & > div {
      width: 90%;
      margin: 0 auto;
      margin-bottom: 3rem;
      .coin_type {
        margin: 0.4rem 0;
        text-align: left;
      }
      .my_wallets {
        margin-bottom: 0.4rem;
        padding: 0.2rem 0.5rem;
        background-color: white;
        & .wallet {
          @include flex_a;
          justify-content: space-between;
          margin-bottom: 0.4rem;
          & > img {
            width: 1.2rem;
          }
        }
        & .line {
          margin: 0 auto;
          height: 1px;
          background-color: #dfdfdf;
        }
        & .wallet_address {
          overflow: hidden;
          border-radius: 5px;
          & > span{
            float: left;
            margin-top: 0.4rem;
            &:first-child {
              margin-right: 0.4rem;
            }
          }
        }
      }
      .tip {
        @include font(0.6rem,#ff0000);
        margin-top: 0.5rem;
        margin-left: 0.3rem;
        text-align: left;
      }
    }
  }
}


</style>



